<template>
  <div id="app">

    <router-view/>
    <!--<router-link to="/a">跳转</router-link>-->
  </div>
</template>

<script>
  // import A from './components/bossActive.vue'
  export default {
    name: 'App',
    methods:{
      setScroll(){
        (/iphone|ipod|ipad/i.test(navigator.appVersion)) && document.addEventListener(
          'blur',
          event => {
            // 当页面没出现滚动条时才执行，因为有滚动条时，不会出现这问题
            // input textarea 标签才执行，因为 a 等标签也会触发 blur 事件
            if (
              document.documentElement.offsetHeight <=
              document.documentElement.clientHeight &&
              ['input', 'textarea'].includes(event.target.localName)
            ) {
              document.body.scrollIntoView() // 回顶部
            }
          },
          true
        )

      }
    },
    mounted(){
      this.setScroll()
    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    /*text-align: center;*/
    color: #2c3e50;
  }
body{
  margin: 0;
}
</style>



